.about{
  background-color: $black;
  &__title{
    margin-bottom: 5px;
    color: $white;
    line-height: 30 / 24 * 100%;
  }
  &__descr{
    max-width: 100%;
    line-height: 28 / 16 * 100%;
    word-wrap: break-word;
    // word-break: break-all;
    color: $light-grey;
  }
  &__subtitle{
    max-width: 100%;
    margin-bottom: 15px;
    @include font-neue;
    font-size: rem(24);
    font-weight: 400;
    color: $white;
    word-wrap: break-word;
    word-break: break-all;
  }
  &__wrapper{
    display: grid;
    grid-template-columns: repeat(2, 1fr );
    gap: 30px;
  }
  &__right{
    display: grid;
    grid-template-rows: 1fr max-content;
  }
  &__form{
    display: grid;
    grid-template-columns: 1fr max-content;
    gap: 30px;
    align-items: center;
  }
  &__picture{
    padding-bottom: 25px;
  }
  &__input{
    width: 100%;
  }
  &__fieldset{
    position: relative;
  }
  &__btn{
    max-width: 130px;
  }
}


@media screen and (max-width: 1024px) {
  .about{
    &__form{
      grid-template-columns: max-content;
      gap: 20px;
    }
    &__input{
      min-width: 342px;
    }
    &__btn{
      width: max-content;
    }
  }
}

@media screen and (max-width: 768px) {
  .about{
    &__wrapper{
      gap: 2.5vw;
    }
    &__right{
      grid-template-rows: 570px max-content;
      align-items: start;
    }
    &__form{
      grid-template-columns: 1fr;
    }
  }
}

@media screen and (max-width: 720px) {
  .about{
    &__wrapper{
      grid-template-columns: 1fr;
    }
    &__right{
      grid-template-rows: 1fr max-content;
      align-items: unset;
    }
    &__input{
      min-width: unset;
    }
    &__picture{
      max-height: 500px;
    }
  }
}

@media screen and (max-width: 576px) {
  .about{
    &__picture{
      min-height: unset;
    }
  }
}
