@import '_variables.scss';

*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
  line-height: 1;
}

html,
body {
	min-height: 100%;
	position: relative;
  color: $black;
}

body {
  font-family: 'Roboto', sans-serif;
	font-size: 1rem;
  min-width: 320px;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article,
aside, canvas, details, embed, figure,
figcaption, footer, header, hgroup,
menu, nav, output, ruby, section,
summary, time, mark, audio, video{
  margin: 0;
  padding: 0;
  border: 0;
  // font-size: 100%;
  vertical-align: baseline;
}

li{
  list-style-type: none;
}

button{
  all: unset;
  @include no-btn
}
.main{
  overflow: hidden;
}
.container{
  min-width: 320px;
  max-width: 1330px;
  margin: 0 auto;
  padding: 0 20px;
}

.visually-hidden{
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
  opacity: 0;
  user-select: none;
  pointer-events: none;
}

@mixin font-neue {
  font-family: 'TTFirsNeue', serif;
}

@mixin h2 {
  font-size: rem(24);
  line-height: 30 / 24 * 100%;
}

@mixin p {
  font-size: rem(16);
  line-height: 22 / 16 * 100%;
}

@mixin over ($line:2){
  // display: box;
  display: -webkit-box;
  display: -moz-box;
  word-wrap: break-word;
  word-break: break-word;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
  -moz-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}


.btn-close{
  position: relative;
  width: rem(18);
  height: rem(18);
  background-color: #c4c4c4;
  border-radius: 50%;
  transition: background-color .35s;
  &::before{
    content: '';
    position: absolute;
    left: 23%;
    top: 48%;
    width: 10px;
    height: 1px;
    background-color: #fff;
    transform: rotate(45deg);
  }
  &::after{
    content: '';
    position: absolute;
    left: 23%;
    top: 48%;
    width: 10px;
    height: 1px;
    background-color: #fff;
    transform: rotate(-45deg);
  }
}

.phone{
  position: relative;
  display: flex;
  align-items: center;
  @include font-neue;
  font-size: rem(14);
  color: $orange;
  text-decoration: none;
  transition: color .35s;
  &__icon{
    display: inline-block;
    svg{
      width: 15px;
      height: 15px;
      margin-right: 5px;
      path{
        fill: $orange;
        transition: fill .35s;
      }
    }
  }
  @media screen and (min-width: 992px) {
    &:hover{
      color: $dark-orange;
      .phone__icon{
        svg{
          path{
            fill: $dark-orange;
          }
        }
      }
    }
  }
  &:focus-visible{
    outline: transparent;
    color: $semi-dark-orange;
    .phone__icon{
      svg{
        path{
          fill: $semi-dark-orange;
        }
      }
    }
  }
  &:active{
    color: $dark-orange;
    .phone__icon{
      svg{
        path{
          fill: $dark-orange;
        }
      }
    }
  }
}

.section{
  padding: rem(40) 0;
  &__title{
    max-width: 100%;
    @include font-neue;
    font-size: rem(24);
    font-weight: 700;
    line-height: 1;
    word-wrap: break-word;
    // word-break: break-all;
  }
}

.category{
  position: absolute;
  display: -webkit-box;
  top: 15px;
  right: 15px;
  max-width: 93%;
  padding: 3px 14px;
  font-size: rem(14);
  line-height: 1.6;
  text-overflow: ellipsis;
  word-wrap: break-word;
  word-break: break-word;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  color: $white;
  text-decoration: none;
  background-color: $grey;
  cursor: default;
}

.btn{
  display: inline-block;
  max-width: 100%;
  padding: rem(14) rem(20);
  color: $white;
  font-weight: 700;
  text-decoration: none;
  word-wrap: break-word;
  background-color: $orange;
  border: 3px solid transparent;
  transition: background-color .35s;
  @media screen and (min-width: 992px) {
    &:hover{
      background-color: $semi-dark-orange;
    }
  }
  &:focus-visible{
    outline: transparent;
    border-color: $dark-orange;
  }
  &:active{
    background-color: $dark-orange;
  }
}

.picture{
  display: block;
  height: 100%;
}

.img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform .55s;
}

.input{
  padding: 15px 25px;
  color: $white;
  border: 1px solid transparent;
  background-color: $light-black;
  transition: color .3s, border-color .3s;
  &::-webkit-input-placeholder{
    color: $light-grey;
    transition: color .3s;
  }
  &::-moz-placeholder{
    color: $light-grey;
    transition: color .3s;
  }
  &:-moz-placeholder{
    color: $light-grey;
    transition: color .3s;
  }
  &:-ms-input-placeholder{
    color: $light-grey;
    transition: color .3s;
  }
  &:focus::-webkit-input-placeholder{
    color: transparent;
  }
  &:focus::-moz-placeholder{
    color: transparent;
  }
  &:focus:-moz-placeholder{
    color: transparent;
  }
  &:focus:-ms-input-placeholder{
    color: transparent;
  }
  @media screen and (min-width: 992px) {
    &:hover::-webkit-input-placeholder{
      color: $orange;
    }
    &:hover::-moz-placeholder{
      color: $orange;
    }
    &:hover:-moz-placeholder{
      color: $orange;
    }
    &:hover:-ms-input-placeholder{
      color: $orange;
    }
  }
  &:focus-visible{
    outline: transparent;
    border-color: $orange;
  }
}


.field-invalid{
  border: 1px solid #f06666;
  &:focus-visible{
    border: 1px solid #f06666;
  }
}
.label-invalid{
  position: absolute;
  top: 4px;
  max-width: 100%;
  padding: 0 25px;
  font-size: 9px;
  color: #f06666 !important;
  word-wrap: break-word;
}
.field-success{
  border: 1px solid $success;
  &:focus-visible{
    border: 1px solid $success;
  }
}
