.footer{
  padding: 25px 0;
  background-color: $black;
  &__wrapper{
    display: grid;
    grid-auto-flow: column;
    // grid-template-columns: repeat(3, 1fr);
    grid-template-columns: 1fr 2fr 165px;
    align-items: center;
  }
  &__logo{
    display: grid;
    grid-template-columns: 1fr;
    .logo{
      margin-bottom: 5px;
      svg{
        width: 191px;
        height: 31px;
      }
    }
  }
  &__logo-copy{
    max-width: 265px;
    font-size: 14px;
    line-height: 18px;
    word-wrap: break-word;
    color: $semi-dark-grey;
  }
  &__descr{
    justify-self: center;
    max-width: 360px;
    word-wrap: break-word;
    color: $light-grey;
    span{
      max-width: inherit;
      display: inline-block;
      padding-top: 7px;
      word-wrap: break-word;
      color: #666;
    }
  }
  &__social{
    display: grid;
    justify-self: end;
    justify-items: center;

  }
  &__social-text{
    max-width: 100%;
    margin-bottom: 10px;
    line-height: 22px;
    @include over;
    word-wrap: break-word;
    color: $semi-dark-grey;
  }
}


@media screen and (max-width: 902px) {
  .footer{
    &__wrapper{
      grid-template-columns: 1fr 1fr;
    }
    &__descr{
      display: none;
    }
  }
}

@media screen and (max-width: 500px) {
  .footer{
    padding-top: 30px;
    padding-bottom: 0;
    &__wrapper{
      grid-template-columns: 1fr;
    }
    &__logo{
      justify-items: center;
      .logo{
        svg{
          width: 35vw;
          height: auto;
        }
      }
    }
    &__logo-copy{
      font-size: 3.5vw;
    }
    &__social{
      justify-self: unset;
      margin-bottom: 30px;
    }
    &__bot{
      padding: 15px 0;
      background-color: $light-black;
    }
  }
}
