.menu{
  padding: 0 0 0 rem(65);
  &__list{
    display: grid;
    grid-auto-flow: column;
    justify-items: center;
    column-gap: rem(10);
  }
  &__link{
    position: relative;
    display: inline-block;
    max-width: 135px;
    padding: rem(5);
    line-height: rem(21);
    @include font-neue;
    text-decoration: none;
    text-align: center;
    word-wrap: break-word;
    color: $white;
    transition: color .45s;
    @media screen and (min-width: 992px) {
      &:hover{
        &:not(:focus-visible){
          color: $orange;
        }
      }
    }
    &:focus-visible{
      outline: transparent;
      z-index: 2;
      &::before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $semi-dark-orange;
        z-index: -1;
      }
    }
    &:active{
      &:not(:focus-visible){
        color: $dark-orange;
      }
    }
  }
}

.burger{
  position: relative;
  width: 20px;
  height: 18px;
  display: none;
  padding: 10px 0;
  svg{
    width: 100%;
    height: 100%;
    rect{
      fill: $white;
      transition: fill .35s;
    }
  }
  &:focus-visible{
    svg{
      rect{
        fill: $semi-dark-orange;
      }
    }
  }
  &:active{
    svg{
      rect{
        fill: $dark-orange;
      }
    }
  }
}
.burger-close{
  display: none;
  margin: 10px 0;
}

.top-phone{
  display: none;
}

@media screen and (max-width: 1200px) {
  .menu{
    padding: 0;
  }
}

@media screen and (max-width: 992px) {
  .menu{
    &__list{
      justify-content: space-between;
    }
    &__link{
      padding: rem(5) 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .menu{
    &__list{
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      row-gap: 20px;
      width: 0;
      height: 0;
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
      user-select: none;
      overflow: auto;
      transform: translate(-120%, 0);
      transition: all .45s;
      &--active{
        width: auto;
        height: calc(100vh - 130px);
        opacity: 1;
        visibility: visible;
        pointer-events: all;
        user-select: auto;
        transform: translate(0);
      }
    }
    &__item{
      width: 100%;
    }
    &__link{
      width: 100%;
      padding: rem(10) 0;
      text-align: start;
    }
    &__phone{
      margin-top: auto;
    }
  }
  .burger{
    display: block;
    &--active{
      display: none;
    }
  }
  .burger-close{
    &--active{
      display: block;
    }
  }
  .top-phone{
    display: flex;
    margin-top: auto;
    margin-bottom: 20px;
    padding: 20px 0;
  }
}
