.logo{
  display: inline-block;
  svg{
    path{
      transition: fill .55s;
    }
  }
  @media screen and (min-width: 992px) {
    &:hover{
      svg{
        path{
          fill: $orange;
        }
      }
      .logo__cube{
        fill: $white;
      }
    }
  }
  &:active{
    @include btn-anim
  }
}



