.contacts{
  &__wrapper{
    display: grid;
    // grid-template-columns: repeat(2, 1fr);
    grid-template-columns: minmax(850px, 2fr) 1fr;
    gap: 30px;
  }
  &__title{
    margin-bottom: 25px;
  }
  &__map{
    position: relative;
    // max-width: 850px;
    min-height: 450px;
    height: 100%;
    overflow: hidden;
  }
  &__info{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 60px 50px 60px 60px;
    max-width: 410px;
    background-color: rgba($black, .85);
    user-select: none;
    pointer-events: none;
    z-index: 10000;
    transform-origin: left bottom;
    transform: scale(0);
    transition: transform .75s;
    &--active{
      top: 0;
      bottom: 0;
      left: 0;
      user-select: auto;
      pointer-events: all;
      transform: scale(1);
    }
  }
  &__info-title{
    max-width: 100%;
    margin-bottom: 5px;
    @include font-neue;
    font-size: 24px;
    font-weight: 400;
    line-height: 1;
    word-wrap: break-word;
    color: $white;
  }
  &__address{
    max-width: 100%;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    line-height: 28px;
    word-wrap: break-word;
    color: $light-grey;
  }
  &__phone{
    max-width: 100%;
    word-wrap: break-word;
    word-break: break-word;
    color: $white;
    .phone__icon{
      opacity: 0.7;
      svg{
        path{
          fill: $white;
        }
      }
    }
    @media screen and (min-width: 992px) {
      &:hover{
        color: $orange;
        .phone__icon{
          svg{
            path{
              fill: $orange;
            }
          }
        }
      }
    }
  }
  &__btn-close{
    position: absolute;
    top: 20px;
    right: 20px;
    width: 20px;
    height: 20px;
    background-color: #666;
    &::before, &::after{
      left: 25%;
      width: 11px;
    }
    @media screen and (min-width: 992px) {
      &:hover{
        background-color: rgba($orange, .8);
      }
    }
    &:focus-visible{
      background-color: $semi-dark-orange;
    }
    &:active{
      background-color: $dark-orange;
    }
  }
  &__show-info{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 15px;
    height: 15px;
    padding: 7px;
    background-color: $orange;
    z-index: 10000;
    transform-origin: left bottom;
    transform: scale(0);
    transition: transform .75s, background-color .55s;
    svg{
      width: 100%;
      height: 100%;
      path{
        fill: $white;
        transition: fill .35s;
      }
    }
    @media screen and (min-width: 992px) {
      &:hover{
        svg{
          path{
            fill: $black;
          }
        }
      }
    }
    &:focus-visible{
      outline: transparent;
      background-color: $semi-dark-orange;
      svg{
        width: 100%;
        height: 100%;
        path{
          fill: $black;
          transition: fill .35s;
        }
      }
    }
    &:active{
      background-color: $dark-orange;
    }
    &--active{
      transform: scale(1);
    }
  }
}

.c-form{
  display: grid;
  &__title{
    min-width: 100%;
    margin-bottom: 5px;
    @include font-neue;
    @include over;
    font-weight: 400;
    font-size: 24px;
    line-height: 1;
    word-wrap: break-word;
  }
  &__descr{
    max-width: 100%;
    margin-bottom: 15px;
    line-height: 22px;
    @include over(5);
    word-wrap: break-word;
    color: $dark-grey;
  }
  &__form{
    display: grid;
    gap: 20px;
    align-items: start;
  }
  &__btn{
    width: max-content;
    max-width: 170px;
  }
  .input{
    width: 100%;
    color: $black;
    background-color: $semi-dark-grey;
    &::-webkit-input-placeholder{
      color: $dark-grey;
      transition: color .3s;
    }
    &::-moz-placeholder{
      color: $dark-grey;
      transition: color .3s;
    }
    &:-moz-placeholder{
      color: $dark-grey;
      transition: color .3s;
    }
    &:-ms-input-placeholder{
      color: $dark-grey;
      transition: color .3s;
    }
    &:focus::-webkit-input-placeholder{
      color: transparent;
    }
    &:focus::-moz-placeholder{
      color: transparent;
    }
    &:focus:-moz-placeholder{
      color: transparent;
    }
    &:focus:-ms-input-placeholder{
      color: transparent;
    }
  }
  &__fieldset{
    position: relative;
    .label-invalid{
      color: $error-dark !important;
    }
    .field-invalid{
      border-color: $error-dark;
    }
  }
  &__area{
    height: auto;
    min-height: 50px;
    resize: vertical;
  }
}

.contact-map{
  position: absolute;
}

[class*="ground-pane"]{
  filter: grayscale(1);
}
[class*="copyrights-pane"]{
  display: none;
}

.ymaps-2-1-79-map-copyrights-promo{
  display: none;
}

.ymaps-2-1-79-copyright__wrap{
  display: none !important;
}


@media screen and (max-width: 1200px) {
  .contacts{
    &__wrapper{
      grid-template-columns: 2fr 1fr;
    }
    &__info{
      max-width: 290px;
      padding: 25px 30px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .contacts{
    &__wrapper{
      grid-template-columns: minmax(590px, 1fr) 1fr;
    }
    &__map{
      height: 100%;
      min-height: 460px;
    }
    &__info{
      top: unset;
      right: 0;
      max-width: unset;
      padding: 25px 30px;
    }
    &__address{
      max-width: 58%;
      margin-bottom: 0;
      .phone{
        display: unset;
        padding-left: 10px;
        font-size: 16px;
        line-height: 21px;
        color: $orange;
        &__icon{
          opacity: 1;
          svg{
            margin-right: 0;
            path{
              fill: $orange;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 902px) {
  .contacts{
    &__wrapper{
      grid-template-columns: 1fr 1fr;
    }
    &__info-title{
      max-width: 80%;
    }
    &__address{
      max-width: 100%;
      .phone{
        padding-left: 0;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .contacts{
    &__wrapper{
      grid-template-columns: 1fr;
    }
    &__address{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      max-width: 65%;
    }
  }
  .c-form{
    max-width: 466px;
  }
}

@media screen and (max-width: 576px) {
  .contacts{
    &__address{
      max-width: 80%;
    }
  }
}

@media screen and (max-width: 320px) {
  .contacts{
    &__map{
      left: -20px;
      right: -20px;
      width: 114.3%;
      min-height: 472px;
    }
    &__info{
      padding: 25px 20px 15px;
    }
    &__info-title{
      font-size: 18px;
      line-height: 22px;
    }
    &__address{
      min-width: 100%;
      font-size: 14px;
      line-height: 18px;
      .phone{
        padding-top: 10px;
      }
    }
  }
  .c-form{
    &__title{
      font-size: 18px;
      line-height: 22px;
    }
    &__descr{
      font-size: 14px;
      line-height: 18px;
    }
    &__form{
      .input{
        padding: 10px 25px;
      }
    }
    &__btn{
      padding: 12px 22px;
    }
  }
}
