.header{
  min-height: rem(80);
  min-width: 320px;
  padding: rem(22) 0;
  background-color: $black;
  overflow: hidden;
  &__wrapper{
    position: relative;
    display: grid;
    grid-template-columns: minmax(auto, rem(200)) 1fr minmax(auto, rem(150));
    align-items: center;
    column-gap: rem(30);
  }
}

.social{
  display: grid;
  grid-template-columns: repeat(3, rem(30));
  grid-column-gap: rem(20);
  justify-content: end;
  &__link{
    display: inline-block;
    width: rem(30);
    height: rem(30);
    @media screen and (min-width: 992px) {
      &:hover{
        .social__svg{
          path{
            fill: $orange;
          }
        }
      }
    }
    &:focus-visible{
      outline: transparent;
      .social__svg{
        path{
          fill: $semi-dark-orange;
        }
      }
    }
    &:active{
      .social__svg{
        path{
          fill: $dark-orange;
        }
      }
    }
  }
  &__svg{
    width: 100%;
    height: 100%;
    path{
      fill: $light-grey;
      transition: fill .45s;
    }
  }
}

.search{
  // position: absolute;
  // right: 0;
  display: none;
  grid-auto-flow: column;
  justify-items: end;
  align-items: center;
  &__btn{
    width: rem(25);
    height: rem(25);
    margin-right: rem(5);
    svg{
      width: 100%;
      height: 100%;
      path{
        width: 100%;
        height: 100%;
        fill: #bcbcbc;
        transition: fill .35s;
      }
    }
    @media screen and (min-width: 992px) {
      &:hover{
        svg{
          path{
            fill: $orange;
          }
        }
      }
    }
    &:focus-visible{
      svg{
        path{
          fill: $semi-dark-orange;
        }
      }
    }
    &:active{
      svg{
        path{
          fill: $dark-orange;
        }
      }
    }
  }
  &__btn-open{
    &--active{
      visibility: hidden;
      opacity: 0;
      pointer-events: none;
      user-select: none;
      z-index: -1;
    }
  }
  &__btn-close{
    display: none;
  }
  &__form{
    position: absolute;
    display: grid;
    grid-auto-flow: column;
    align-items: center;
    width: 0;
    padding: rem(25);
    background-color: $black;
    transform: translate(rem(138), 0);
    opacity: 0;
    // visibility: hidden;
    z-index: -1;
    user-select: none;
    pointer-events: none;
    transition: all .45s;
    &--active{
      width: auto;
      opacity: 1;
      // visibility: visible;
      user-select: auto;
      pointer-events: all;
      z-index: auto;
      transform: translate(0, 0);
    }
  }
  &__input{
    max-width: rem(170);
    padding: 2px;
    font-size: rem(14);
    color: $white;
    background-color: transparent;
    border: unset;
    border-bottom: 1px solid $semi-dark-grey;
    transition: border-color .35s;
    &::-webkit-input-placeholder{
      color: $white;
      transition: color .3s;
    }
    &::-moz-placeholder{
      color: $white;
      transition: color .3s;
    }
    &:-moz-placeholder{
      color: $white;
      transition: color .3s;
    }
    &:-ms-input-placeholder{
      color: $white;
      transition: color .3s;
    }
    &:focus::-webkit-input-placeholder{
      color: transparent;
    }
    &:focus::-moz-placeholder{
      color: transparent;
    }
    &:focus:-moz-placeholder{
      color: transparent;
    }
    &:focus:-ms-input-placeholder{
      color: transparent;
    }
    @media screen and (min-width: 992px) {
      &:hover{
        border-bottom-color: $orange;
      }
    }
    &:focus-visible{
      outline: transparent;
      border-bottom-color: $orange;
    }
    &:active{
      border-bottom-color: $dark-orange;
    }
  }
}

.header-bottom{
  display: none;
  min-height: rem(45);
  min-width: 320px;
  padding: rem(7) 0;
  background-color: $light-black;
  transition: background-color .35s;
  z-index: 1000;
}

@media screen and (max-width: 1024px) {
  .header{
    &__wrapper{
      grid-template-columns: minmax(auto,12.5rem) 1fr minmax(auto, rem(40));
    }
    &__social{
      display: none;
    }
    .search{
      display: grid;
    }
  }
}

@media screen and (max-width: 992px) {
  .header-bottom{
    display: block;
  }
  .header{
    &__wrapper{
      grid-template-columns: minmax(rem(200), 1fr) minmax(rem(40), 1fr);
    }
  }
}

@media screen and (max-width: 576px) {
  .header-bottom{
    &--active{
      position: absolute;
      left: 0;
      right: 0;
      background-color: $black;
    }
  }
  .search{
    &__form{
      grid-template-columns: 1fr;
      width: 100%;
      padding: rem(25) 0;
    }
    &__btn-submit{
      display: none;
    }
    &__btn-close{
      display: block;
    }
    &__input{
      max-width: 95%;
    }
  }
}
