.hero{
  &__wrapper{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: minmax(18px, max-content) 300px minmax(257px, 1fr);
    gap: 30px;
    grid-template-areas:
      "big big title title"
      "big big semi semi"
      "big big mini mini2";
  }
  &__title{
    grid-area: title;
    max-width: 630px;
    margin-top: -6px;
  }
  &__card{
    position: relative;
    display: inline-block;
    height: 100%;
    overflow: hidden;
    @media screen and (min-width: 992px) {
      &:hover{
        .hero__img{
          transform: scale(1.05);
        }
      }
    }
  }
  &__card-big{
    grid-area: big;
  }
  &__card-semi{
    grid-area: semi;
  }
  &__card-mini{
    grid-area: mini;
  }
  &__card-mini2{
    grid-area: mini2;
  }
  &__info{
    position: absolute;
    left: 60px;
    right: 60px;
    bottom: 60px;
  }
  &__picture{
    display: block;
    height: 100%;
  }
  &__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    transition: transform .55s;
  }
  &__info-descr{
    max-width: 90%;
    margin-bottom: rem(20);
    @include font-neue;
    font-size: rem(36);
    line-height: rem(37);
    color: $white;
    word-wrap: break-word;
  }
  &__info-name{
    display: inline-block;
    max-width: 100%;
    margin-bottom: 3px;
    color: $white;
    word-wrap: break-word;
  }

}



@media screen and (max-width: 1200px) {
  .hero{
    padding: 4vw 0;
    &__wrapper{
      gap: 2.5vw;
      grid-template-rows: minmax(18px, max-content) 228px minmax(133px, 1fr);
    }
    &__info-descr{
      font-size: 2.3vw;
      line-height: 2.5vw;
      margin-bottom: 1.9vw;
    }
    &__info-name{
      font-size: 1.2vw;
    }
    &__btn{
      padding: 1.4vw 1.8vw;
      font-size: 1.4vw;
    }
    &__info{
      left: 4vw;
      right: 4vw;
      bottom: 4vw;
    }
    &__title{
      margin-top: 0;
      font-size: 2.6vw;
    }
  }
}

@media screen and (max-width: 576px) {
  .hero{
    &__wrapper{
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: auto;
      grid-template-areas:
      "big big"
      "big big"
      "title title"
      "semi semi"
      "mini mini2";
    }
    &__left{
      margin-bottom: 2vw;
    }
    &__title{
      margin-bottom: 0;
      font-size: 5.5vw;
    }
    &__info{
      left: 5vw;
      right: 5vw;
      bottom: 5vw;
    }
    &__info-descr{
      font-size: 5.5vw;
      line-height: 6.5vw;
      margin-bottom: 4vw;
    }
    &__info-name{
      font-size: 2.6vw;
    }
    &__btn{
      padding: 2.4vw 3vw;
      font-size: 3vw;
    }
  }
}
