.projects{
  &__wrapper{
    display: grid;
    grid-auto-flow: row dense;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
  }
  &__card1{
    grid-column: 1 / 3;
  }
  &__card2{
    grid-column: 3 / 5;
  }
  &__card3{
    grid-column: 5 / 7;
  }
  &__card4{
    grid-column: 7 / 13;
  }
  &__card5{
    grid-column: 1 / 7;
  }
  &__card6{
    grid-column: 7 / 10;
  }
  &__card7{
    grid-column: 10 / 13;
  }
  &__title{
    margin-bottom: 15px;
  }
  &__link-img{
    position: relative;
    display: block;
    margin-bottom: 15px;
    @media screen and (min-width: 992px) {
      &:hover ~ .projects__card-title{
        color: $orange;
      }
    }
  }
  &__picture{
    min-height: 190px;
  }
  &__category{
    top: 7px;
    right: 7px;
    &::before{
      content: '#';
      margin-right: -2px;
    }
  }
  &__card-title{
    position: relative;
    display: -webkit-box;
    max-width: 100%;
    max-height: 63px;
    margin-bottom: 12px;
    @include p;
    text-overflow: ellipsis;
    word-wrap: break-word;
    color: $black;
    text-decoration: none;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
    transition: color .35s, background-color .35s;
    &::before{
      // content: '';
      position: absolute;
      top: 0;
      right: -5px;
      bottom: 0;
      left: -5px;
      background-color: $light-orange;
      z-index: -1;
    }
    @media screen and (min-width: 992px) {
      &:hover{
        color: $orange;
      }
    }
    &:focus-visible{
      outline: transparent;
      &::before{
        content: '';
      }
    }
    &:active{
      color: $dark-orange;
    }
  }
  &__date{
    display: flex;
    align-items: center;
  }
  &__date-icon{
    display: inline-block;
    width: 14px;
    height: 14px;
    margin-right: 4px;
  }
  &__date-time{
    display: -webkit-box;
    max-width: 85%;
    font-size: 12px;
    line-height: 22px;
    word-wrap: break-word;
    word-break: break-word;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    overflow: hidden;
    color: $dark-grey;
  }
  &__big-info{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 30px;
    height: 100%;
    cursor: default;
    .projects__date-time{
      color: $white;
    }
  }
  &__big-title{
    display: box;
    display: -webkit-box;
    max-width: 45%;
    margin-bottom: 10px;
    @include font-neue;
    font-size: 24px;
    line-height: 1;
    text-overflow: ellipsis;
    word-wrap: break-word;
    word-break: break-word;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    color: $white;
  }
  &__big-descr{
    max-width: 51.2%;
    margin-bottom: 10px;
    @include p;
    word-wrap: break-word;
    color: $white;
  }
  &__btn{
    margin-top: auto;
    padding: 8px 20px;
    line-height: 175%;
    @include over(1);
    background-color: transparent;
    border-color: $orange;
    transition: box-shadow .45s;
    @media screen and (min-width: 992px) {
      &:hover{
        background-color: unset;
        box-shadow: inset 0 -5rem 0 0 $orange;
      }
    }
    &:focus-visible{
      background-color: $semi-dark-orange;
    }
    &:active{
      background-color: $dark-orange;
      border-color: transparent;
    }
  }
}


.p-card{
  &__mini{
    display: grid;
    // max-width: 190px;
  }
  &__semi{
    display: grid;
  }
  &__big{
    position: relative;
    display: grid;
    // max-width: 630px;
  }
}

@media screen and (max-width: 1024px) {
  .projects{
    &__wrapper{
      grid-template-columns: repeat(8,1fr);
    }
    &__card1{
      grid-column: 1 / 3;
    }
    &__card2{
      grid-column: 3 / 5;
    }
    &__card3{
      grid-column: 5 / 9;
    }
    &__card4{
      grid-column: 1 / 6;
      grid-row: 2 / 3;
    }
    &__card5{
      grid-column: 4 / 9;
      grid-row: 3 / 4;
    }
    &__card6{
      grid-column: 6 / 9;
      grid-row: 2 / 3;
    }
    &__card7{
      grid-column: 1 / 4;
      grid-row: 3 / 4;
    }
    &__card-title{
      max-height: 44px;
    }
    &__big-title{
      min-width: 50%;
    }
    &__big-descr{
      max-width: 54.2%;
    }
    &__picture{
      min-height: 184px;
    }
  }
  .p-card{
    &__mini{
      .projects__picture{
        max-height: 184px;
      }
    }
    &__semi{
      .projects__picture{
        max-height: 184px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .projects{
    &__wrapper{
      grid-template-columns: repeat(6,1fr);
    }
    &__card1{
      grid-column: 1 / 3;
    }
    &__card2{
      grid-column: 3 / 5;
    }
    &__card3{
      grid-column: 5 / 7;
    }
    &__card4{
      grid-column: 1 / 7;
      grid-row: 2 / 3;
    }
    &__card5{
      grid-column: 1 / 7;
      grid-row: 4 / 5;
    }
    &__card6{
      grid-column: 1 / 4;
      grid-row: 3 / 4;
    }
    &__card7{
      grid-column: 4 / 7;
      grid-row: 3 / 4;
    }
    &__card-title{
      max-height: 44px;
    }
    &__big-title{
      min-width: 50%;
    }
    &__big-descr{
      max-width: 44.2%;
    }
    &__picture{
      min-height: 184px;
    }
  }
}

@media screen and (max-width: 576px) {
  .projects{
    &__wrapper{
      grid-template-columns: repeat(4,1fr);
    }
    &__card1{
      grid-column: 1 / 3;
    }
    &__card2{
      grid-column: 3 / 5;
    }
    &__card3{
      grid-column: 1 / 5;
      grid-row: 2 / 3;
    }
    &__card4{
      grid-column: 1 / 5;
      grid-row: 3 / 4;
    }
    &__card5{
      grid-column: 1 / 5;
      grid-row: 5 / 6;
    }
    &__card6{
      grid-column: 1 / 3;
      grid-row: 4 / 5;
    }
    &__card7{
      grid-column: 3 / 5;
      grid-row: 4 / 5;
    }
    &__card-title{
      max-height: 44px;
    }
    &__biginfo{
      padding: 4.5vw;
    }
    &__big-title{
      min-width: 50%;
      font-size: 4.2vw;
    }
    &__big-descr{
      max-width: 65%;
      font-size: 2.5vw;
    }
    &__picture{
      min-height: 184px;
    }
    &__btn{
      padding: 1.8vw 2.8vw;
      font-size: 2.8vw;
    }
  }
}

@media screen and (max-width: 320px) {
  .projects{
    &__wrapper{
      grid-template-columns: repeat(2,1fr);
    }
    &__card1{
      grid-column: 1 / 3;
    }
    &__card2{
      grid-column: 1 / 3;
      grid-row: 2 / 3;
    }
    &__card3{
      grid-column: 1 / 3;
      grid-row: 3 / 4;
    }
    &__card4{
      grid-column: 1 / 3;
      grid-row: 4 / 6;
    }
    &__card5{
      grid-column: 1 / 3;
      grid-row: 8 / 10;
    }
    &__card6{
      grid-column: 1 / 3;
      grid-row: 6 / 7;
    }
    &__card7{
      grid-column: 1 / 3;
      grid-row: 7 / 8;
    }
    &__card-title{
      max-height: 44px;
    }
    &__big-info{
      top: unset;
      bottom: 0;
      height: unset;
    }
    &__big-title{
      min-width: 100%;
      font-size: 24px;
      line-height: 24px;
    }
    &__big-descr{
      max-width: 85%;
      margin-bottom: 15px;
      font-size: 14px;
      line-height: 18px;
    }
    &__picture{
      min-height: 184px;
    }
    &__btn{
      padding: 12px 20px;
      font-size: 14px;
      line-height: 16px;
    }
  }
  .p-card{
    &__big{
      min-height: 422px;
    }
  }
}
